div .name-card { /* can use another selector, just wanted it to be exceptionally clear what i was referring to here */
    white-space:nowrap;
    overflow:hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    max-width: 200px;
}

@media (max-width: 840px) 
{
	div .name-card {
		max-width: 159px
	}
}

div .artist-card {
	display: -ms-flexbox;
	display: flex;
	overflow: unset;
	padding-left: 30px;
	margin: 10px;
}

.artist-card-image {
	-o-object-fit: cover;
	   object-fit: cover;
	position: relative;
	bottom: 40px;
}

div .artist-card-name:last-child {
	padding-top: unset;
	padding-bottom: unset;
	padding-left: unset;
}

.card-container {
	max-width: 500px;
}

.card-actions {
	margin-right: 25px;
}

.heading-artists {
	margin-bottom: 5%;
	margin-top: 5%;
}

.subheading-artists {
	margin-bottom: 3%;
	position: relative;
}

.question-mark {
	position: relative;
	top:3px;
}

@media (max-width: 640px) 
{
    div .second-table
    {
        display: none;
    }
}

/* Iphone SE */
@media (max-width: 342px) 
{
	.artist-card-genre-grid {
		display: none;
	}

	div .artist-card {
		margin: 5px;
	}
}

/* Small phone */


@media (max-width: 400px) 
{
	.MuiBox-root.heading-artists > h2 {
		font-size: 2rem;
	}
}

/* IPAD */
@media (max-width: 800px) 
{
	.heading-artists > h2 {
		max-width: 400px;
	}
}


.artist-metal-text {
	-webkit-text-decoration-style: dotted;
	        text-decoration-style: dotted;
}

.artist-card-genre {
	color:grey;
}

.fontsize-heading {
	font-size: 3em;	
}

.artist-container {
	margin-top: 40px;
}

/*Genres Page*/

div .genres-heading {
	margin-top: 5%;
}

div .genres-table {
	margin-top: 7%;
	margin-bottom: 2%;
}

.genres-main {
    min-height: 105vh;
    background-color: black;
    width: 100%;
}

.genres-image {
	position: relative;
	top:20px;
}

.genres-color {
	color: white !important;
}

.fontsize-genres  {
	letter-spacing: 3px;
}

/* Footer */

.footer {
	background-color: black;
	color: white;
	min-height: 150px;
}

.footer-actions {
	margin: 3vh;
}

.footer-actions > a {
    text-decoration: inherit;
    color: inherit;
    cursor: auto;
}

.footer-actions > a.nostyle:visited {
    text-decoration: inherit;
    color: inherit;
    cursor: auto;
}

.footer-actions:first-child {
	margin-top: 6vh;
}


/*tiny letter*/
.tiny-from {
	border:1px solid #ccc;
	padding:3px;
	text-align:center;
}

