

.heading {
	color: white;
	font-size: 30px;
	margin: 40px 50px 50px 50px;
}



.sub-heading {
	font-size: 35vw;
	margin-top: 1em;
	color: white;
}

.login-box {
	max-width: 430px;
	padding: 5vw;
}

.login-box > h4 {
	letter-spacing: 2px;
	color: white;
	font-weight: 800;
}

.login-button {

	border: 4px solid white;
	background: 0 0;
	color: white;
	padding: 14px 28px;
	font-size: 22px;
	cursor: pointer;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

